// 设置页面Html的基本样式

html {
  box-sizing: border-box;
  height: 100%;
}

body {
  background-color: $themecolor;
  color: $white;
  display: flex;
  flex-direction: row;
  font-family: $body-family;
  font-size: 65%;
  line-height: 1.6;
  margin: 0;
  min-height: 100%;
}


